<template>
  <div>
    <div class="headdiv"><span>大老齐养狗基地</span></div>
    
    <div class="bottomdiv">
      <div class="choose" @click="changeto"><img src="../assets/img1.png"><div class="c-font">查看狗狗</div></div>
      <div class="choose choose1"><img src="../assets/img2.png"><div class="c-font">联系我们</div></div>
    </div>

    <div class="list">
      <img src="../assets/addr.png">
      <div class="fontc">地址：河北省黄骅市齐赵村248乡道北50米</div>
      <div class="fontc">联系电话：15720364516</div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'ShowList',
  data () {
    return {
    }
  },
  methods:{
    changeto(){
      this.$router.push({ name: 'ShowList'})
    },
  },
}
</script>

<style scoped>
.headdiv{
  width: 100%;
  height: 40px;
  text-align: center;
  margin: 0 auto;
  padding-top: 11px;
  background-color: #409eff;
}
.headdiv span{
  font-family: "Helvetica Neue","Helvetica","PingFang SC","Hiragino Sans GB","Microsoft YaHei","SimSun","sans-serif";
  font-size: 18px;
  color: #ffffff;
}
.bottomdiv{
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 50px;
  display: flex;
  background-color: #f2f6fc;
}
.bottomdiv .choose{
  width: 100%;
  height: 50px;
  padding: 12px;
  font-family: "Helvetica Neue","Helvetica","PingFang SC","Hiragino Sans GB","Microsoft YaHei","SimSun","sans-serif";
  font-size: 14px;
  color: #606266;
  text-align: center;
  margin: 0 auto;
}
.bottomdiv .choose1{
  background-color: #e4e7ed;
}
.bottomdiv .choose .c-font{
  font-family: "Helvetica Neue","Helvetica","PingFang SC","Hiragino Sans GB","Microsoft YaHei","SimSun","sans-serif";
  font-size: 14px;
  color: #606266;
}

.list{
  margin: 20px 20px 0 20px;
}
.list img{
  width: 100%;
}
.list .fontc{
  font-family: "Helvetica Neue","Helvetica","PingFang SC","Hiragino Sans GB","Microsoft YaHei","SimSun","sans-serif";
  font-size: 22px;
  color: #606266;
  margin-top: 20px;
}
</style>



