<template>
  <div>
    <div class="headdiv"><span>大老齐养狗基地</span></div>
    
    <div class="bottomdiv">
      <div class="choose" @click="changeto"><img src="../assets/img3.png"><div class="c-font">添加</div></div>
      <div class="choose choose1"><img src="../assets/img4.png"><div class="c-font">删除</div></div>
    </div>

    <div class="list">
      <el-scrollbar style="height:100%">
        <div class="listnode" v-for='(nod, key, index) in datalist' :key="index">
          <el-image :src="nod.url" fit="contain" lazy></el-image>
          <div>{{nod.text}}</div>
          <div class="nodebut"><el-button type="primary" @click="del(nod.Id,index)">删除</el-button></div>
        </div>
      </el-scrollbar>
      <div style="width:100%;height:60px;"></div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Del',
  data () {
    return {
      datalist:[],
    }
  },
  mounted(){
    this.init();
  },
  methods:{
    init(){
      var that = this;
      this.$axios.post('/getall', {})
      .then(function (response) {
        that.datalist = response.data.data;
      })
      .catch(function (error) {
        console.log(error);
        that.$message({
          message: '连接服务器失败',
          duration: 3000,
        });
      });
    },
    changeto(){
      this.$router.push({ name: 'Add'});
    },
    del(id,index){
      var that = this;
      this.$axios.post('/del', {
        id : id,
      })
      .then(function (response) {
        if(response.data.code==1){
          that.$message({
            message: '删除成功',
            duration: 3000,
          });
          that.datalist.splice(index,1);
        }else{
          that.$message({
            message: '删除失败',
            duration: 3000,
          });
        }
      })
      .catch(function (error) {
        console.log(error);
        that.$message({
          message: '连接服务器失败',
          duration: 3000,
        });
      });
    },
  },
}
</script>

<style scoped>
.headdiv{
  width: 100%;
  height: 40px;
  text-align: center;
  margin: 0 auto;
  padding-top: 11px;
  background-color: #409eff;
}
.headdiv span{
  font-family: "Helvetica Neue","Helvetica","PingFang SC","Hiragino Sans GB","Microsoft YaHei","SimSun","sans-serif";
  font-size: 18px;
  color: #ffffff;
}
.bottomdiv{
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 50px;
  display: flex;
  background-color: #f2f6fc;
  z-index: 1;
}
.bottomdiv .choose{
  width: 100%;
  height: 50px;
  padding: 12px;
  font-family: "Helvetica Neue","Helvetica","PingFang SC","Hiragino Sans GB","Microsoft YaHei","SimSun","sans-serif";
  font-size: 14px;
  color: #606266;
  text-align: center;
  margin: 0 auto;
}
.bottomdiv .choose1{
  background-color: #e4e7ed;
}
.bottomdiv .choose .c-font{
  font-family: "Helvetica Neue","Helvetica","PingFang SC","Hiragino Sans GB","Microsoft YaHei","SimSun","sans-serif";
  font-size: 14px;
  color: #606266;
}

.list{
  width: 100%;
  height: 100%;
}
.listnode{
  margin: 20px 20px 0 20px;
  padding-bottom: 10px;
  border-radius: 5px;
  border-bottom: 1px solid #c0c4cc;
  overflow: hidden;
}
.listnode span{
  font-family: "Helvetica Neue","Helvetica","PingFang SC","Hiragino Sans GB","Microsoft YaHei","SimSun","sans-serif";
  font-size: 16px;
}
.nodebut{
  text-align: center;
  margin: 0 auto;
  margin-top: 10px;
}
</style>



