<template>
  <div>
    <div class="headdiv"><span>大老齐养狗基地</span></div>
    
    <div class="bottomdiv">
      <div class="choose choose1"><img src="../assets/img3.png"><div class="c-font">添加</div></div>
      <div class="choose" @click="changeto"><img src="../assets/img4.png"><div class="c-font">删除</div></div>
    </div>

    <div class="list">
      <div class="sitelogo">
        <img :src="imgsrc">
      </div>
      <div class="sitelogo">
        <el-input
          type="textarea"
          :rows="4"
          placeholder="请输入内容"
          v-model="updata.text">
        </el-input>
      </div>
    <el-upload
      class="upload-demo"
      :multiple="false"
      ref="upload"
      action="/uploadimg"
      :data='updata'
      :on-preview="handlePreview"
      :on-remove="handleRemove"
      :on-change="chooseimg"
      :on-exceed="changeimg"
      :on-success="uploadsuccess"
      :on-error="uploaderror"
      :show-file-list="false"
      accept="image/png, image/jpeg, image/jpg"
      :auto-upload="false">
      <el-button slot="trigger" size="small" type="primary">选取文件</el-button>
      <el-button style="margin-left: 10px;" size="small" type="success" @click="submitUpload">上传到服务器</el-button>
      <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过2MB</div>
    </el-upload>
    <div style="width:100%;height:60px;"></div>
    </div>
    
  </div>
</template>

<script>
export default {
  name: 'Add',
  data () {
    return {
      imgsrc:'',
      updata:{
        text:'',
      },
    }
  },
  methods:{
    changeto(){
      this.$router.push({ name: 'Del'});
    },
    submitUpload() {
      if(this.imgsrc == ''){
        this.$message({
          message: '请选择图片',
          type: 'warning'
        });
      }else if(this.updata.text == ''){
        this.$message({
          message: '请填写说明',
          type: 'warning'
        });
      }else{
        this.$refs.upload.submit();
      }
    },
    handleRemove(file, fileList) {
    },
    handlePreview(file) {
    },
    chooseimg(file, fileList){
      this.imgsrc = window.URL.createObjectURL(file.raw);
      if(fileList.length > 1){
        fileList.shift();
      }
    },
    changeimg(files, fileList){
      fileList[0].raw = files[0];
      this.imgsrc = window.URL.createObjectURL(files[0]);
    },
    uploadsuccess(response, file, fileList){
      this.$message({
        type: 'success',
        message: '上传成功!'
      });
      this.imgsrc = '';
      this.updata.text = '';
    },
    uploaderror(err, file, fileList){
      this.$message({
        type: 'error',
        message: '上传失败!'
      });
    },
  },
}
</script>

<style scoped>
.headdiv{
  width: 100%;
  height: 40px;
  text-align: center;
  margin: 0 auto;
  padding-top: 11px;
  background-color: #409eff;
}
.headdiv span{
  font-family: "Helvetica Neue","Helvetica","PingFang SC","Hiragino Sans GB","Microsoft YaHei","SimSun","sans-serif";
  font-size: 18px;
  color: #ffffff;
}
.bottomdiv{
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 50px;
  display: flex;
  background-color: #f2f6fc;
  z-index: 1;
}
.bottomdiv .choose{
  width: 100%;
  height: 50px;
  padding: 12px;
  font-family: "Helvetica Neue","Helvetica","PingFang SC","Hiragino Sans GB","Microsoft YaHei","SimSun","sans-serif";
  font-size: 14px;
  color: #606266;
  text-align: center;
  margin: 0 auto;
}
.bottomdiv .choose1{
  background-color: #e4e7ed;
}
.bottomdiv .choose .c-font{
  font-family: "Helvetica Neue","Helvetica","PingFang SC","Hiragino Sans GB","Microsoft YaHei","SimSun","sans-serif";
  font-size: 14px;
  color: #606266;
}

.list{
  margin: 20px 20px 0 20px;
}

.sitelogo{
  display: block;
  width: 100%;
  margin-bottom: 20px;
}
.sitelogo img{
  display: block;
  max-width: 100%;
}
</style>




