<template>
  <div>
    <div class="headdiv"><span>大老齐养狗基地</span></div>
    
    <div class="bottomdiv">
      <div class="choose choose1"><img src="../assets/img1.png"><div class="c-font">查看狗狗</div></div>
      <div class="choose" @click="changeto"><img src="../assets/img2.png"><div class="c-font">联系我们</div></div>
    </div>

    <div class="list">
      <el-scrollbar style="height:100%">
        <div class="listnode" v-for='(nod, key, index) in datalist' :key="index">
          <el-image :src="nod.url" fit="contain" lazy></el-image>
          <span>{{nod.text}}</span>
        </div>
      </el-scrollbar>
      <div class="market">冀ICP备19017406</div>
      <div style="width:100%;height:60px;"></div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'ShowList',
  data () {
    return {
      datalist:[],
    }
  },
  mounted(){
    this.init();
  },
  methods:{
    init(){
      var that = this;
      this.$axios.post('/getall', {})
      .then(function (response) {
        that.datalist = response.data.data;
      })
      .catch(function (error) {
        console.log(error);
        that.$message({
          message: '连接服务器失败',
          duration: 3000,
        });
      });
    },
    changeto(){
      this.$router.push({ name: 'Map'})
    },
  },
}
</script>

<style scoped>
.headdiv{
  width: 100%;
  height: 40px;
  text-align: center;
  margin: 0 auto;
  padding-top: 11px;
  background-color: #409eff;
}
.headdiv span{
  font-family: "Helvetica Neue","Helvetica","PingFang SC","Hiragino Sans GB","Microsoft YaHei","SimSun","sans-serif";
  font-size: 18px;
  color: #ffffff;
}
.bottomdiv{
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 50px;
  display: flex;
  background-color: #f2f6fc;
  z-index: 1;
}
.bottomdiv .choose{
  width: 100%;
  height: 50px;
  padding: 12px;
  font-family: "Helvetica Neue","Helvetica","PingFang SC","Hiragino Sans GB","Microsoft YaHei","SimSun","sans-serif";
  font-size: 14px;
  color: #606266;
  text-align: center;
  margin: 0 auto;
}
.bottomdiv .choose1{
  background-color: #e4e7ed;
}
.bottomdiv .choose .c-font{
  font-family: "Helvetica Neue","Helvetica","PingFang SC","Hiragino Sans GB","Microsoft YaHei","SimSun","sans-serif";
  font-size: 14px;
  color: #606266;
}

.list{
  width: 100%;
  height: 100%;
}
.listnode{
  margin: 20px 20px 0 20px;
  padding-bottom: 10px;
  border-radius: 5px;
  border-bottom: 1px solid #c0c4cc;
  overflow: hidden;
}
.listnode span{
  font-family: "Helvetica Neue","Helvetica","PingFang SC","Hiragino Sans GB","Microsoft YaHei","SimSun","sans-serif";
  font-size: 16px;
}
.market{
  width: 100%;
  font-family: "Helvetica Neue","Helvetica","PingFang SC","Hiragino Sans GB","Microsoft YaHei","SimSun","sans-serif";
  font-size: 14px;
  color:#909399;
  text-align: center;
  margin: 0 auto;
  margin-top: 10px;
}
</style>



