<template>
<div>
  <div class="back">
    <img class="top" src="../assets/back2.jpg" />
  </div>
  <div class="cont">
    <el-form ref="form" :model="form" label-width="25%">
      <el-form-item label="用户名">
        <el-input v-model="form.username" placeholder="请输入用户名"></el-input>
      </el-form-item>
      <el-form-item label="密码">
        <el-input show-password v-model="form.password" placeholder="请输入密码"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" size="medium" @click="onSubmit">登录</el-button>
      </el-form-item>
    </el-form>
  </div>
  
</div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      form:{
        username: '',
        password: '',
      },
    }
  },
  mounted(){
  },
  methods:{
    onSubmit() {
      var that = this;
      this.$axios.post('/login', {
        username :this.form.username,
        password :this.form.password,
      })
      .then(function (response) {
        if(response.data.code==1){
          that.$message({
            message: '登录成功',
            duration: 3000,
          });
          localStorage.setItem("usr",that.form.username);
          that.form.username = '';
          that.form.password = '';
          that.$router.push({ name: 'Add'});
        }else if(response.data.code==0 || response.data.code==2){
          that.$message({
            message: '用户名密码错误',
            duration: 3000,
          });
        }else{
          that.$message({
            message: '服务错误',
            duration: 3000,
          });
        }
      })
      .catch(function (error) {
        console.log(error);
        that.$message({
          message: '连接服务器失败',
          duration: 3000,
        });
      });
    },
  },
}
</script>

<style scoped>
.back{
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  margin: 0 auto;
}
.top{
  height: 100%;
  /* width: 100%; */
  /* margin-bottom: 20px; */
}
.cont{
  position: absolute;
  top: 30%;
  width: 80%;
  margin-left: 5%;
}
.but{
  display: inline-block;
  line-height: 1;
  white-space: nowrap;
  cursor: pointer;
  background: #fc4101;
  border: 1px solid #fc4101;
  color: #FFF;
  -webkit-appearance: none;
  text-align: center;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  outline: 0;
  margin: 0;
  -webkit-transition: .1s;
  transition: .1s;
  font-weight: 500;
  -ms-user-select: none;
  padding: 12px 20px;
  font-size: 14px;
  border-radius: 4px;
}
</style>

<style>

/* .el-button--medium {
    padding: 16px 26px;
    font-size: 16px;
    border-radius: 4px;
} */
.el-button--medium {
  width: 100%;
    padding: 11px;
    font-size: 16px;
    border-radius: 4px;
}
</style>



